import React, { useCallback, useEffect, useState } from "react";
import cx from "classnames";
import {
  AnimationProps,
  DraggableProps,
  DragHandlers,
  HoverHandlers,
  motion,
  PanInfo,
} from "framer-motion";

import switchImgUrl from "./switch.png";
import "./App.css";
import "./Reset.css";

const GENERATIVE_ART_URL = "https://albertqm.github.io/generative-art/";

export default function App() {
  const [thingToDo, setThingToDo] = useState<string | undefined>();
  const [hoverColor, setHoverColor] = useState(getRandomColor());

  const [isLightOn, setIsLightOn] = useState(true);
  const [hasActivatedSwitch, setHasActivatedSwitch] = useState(false);
  const handleDrag: DragHandlers["onDrag"] = useCallback(
    (_e, { point: { y } }) => {
      if (hasActivatedSwitch) {
        return;
      }

      if (y > 200) {
        setHasActivatedSwitch(true);
        setIsLightOn((prev) => !prev);
      }
    },
    [hasActivatedSwitch]
  );

  useEffect(() => {
    (async () => {
      try {
        const resp = await fetch("https://www.boredapi.com/api/activity");
        const { activity }: { activity: string } = await resp.json();
        setThingToDo(activity.toLowerCase());
      } catch (error) {
        setThingToDo(
          "fix the API Albert is using on this website to show a new activity every time"
        );
      }
    })();
  }, []);

  return (
    <div style={{ filter: `brightness(${isLightOn ? 1 : 0.5})` }}>
      <motion.img
        src={switchImgUrl}
        drag="y"
        whileTap={{ cursor: "grabbing" }}
        dragMomentum={false}
        dragElastic={0.1} // Make it hard to drag far away (and expose top of switch)
        dragConstraints={{ top: 0, bottom: 25 }}
        style={{
          position: "absolute",
          top: -150,
          left: 25,
          width: 100,
        }}
        onDrag={handleDrag}
        onDragEnd={() => setHasActivatedSwitch(false)}
      ></motion.img>
      <div className="container">
        <Avatar />
        <div className="intro-container">
          {`Hey, I'm Albert. 
          I mostly create Web-related products for a living.`}
        </div>
      </div>
      <ProjectPoster
        whileHover="actionButton"
        variants={{ actionButton: { backgroundColor: hoverColor } }}
        onMouseLeave={() => setHoverColor(getRandomColor())}
        onClick={() => (window.location.href = GENERATIVE_ART_URL)}
      >
        <ProjectTitle
          title={
            <motion.a
              href={GENERATIVE_ART_URL}
              className="project-title"
              variants={{ actionButton: { scale: 1.2 } }}
            >
              Generative Art
            </motion.a>
          }
        />
      </ProjectPoster>
      <ProjectPoster>
        <ActivitySuggestion thingToDo={thingToDo} />
      </ProjectPoster>
      <ProjectPoster>
        <ProjectTitle
          title={
            <motion.span
              transition={{ repeat: Infinity, duration: 2 }}
              animate={{ rotate: [-10, 10, -10] }}
            >
              More stuff to come...
            </motion.span>
          }
        />
      </ProjectPoster>
    </div>
  );
}

function Avatar() {
  return (
    <div className="avatar-container">
      <motion.img
        className="avatar-img"
        src="https://avatars.githubusercontent.com/u/33761650?v=4"
        alt="A photo of Albert's face"
        whileHover={{
          rotate: 360,
        }}
        whileTap={{ rotate: 360 }}
        transition={{
          type: "spring",
        }}
      />
    </div>
  );
}

function ProjectPoster({
  children,
  whileHover,
  variants,
  onClick,
  onMouseLeave,
}: {
  children: React.ReactNode;
  whileHover?: HoverHandlers["whileHover"];
  variants?: AnimationProps["variants"];
  onClick?: () => void;
  onMouseLeave?: () => void;
}) {
  return (
    <motion.div
      className={cx("project-poster", { clickable: Boolean(onClick) })}
      whileHover={whileHover}
      variants={variants}
      onMouseLeave={onMouseLeave}
      onClick={onClick}
    >
      {children}
    </motion.div>
  );
}

function ProjectTitle({ title }: { title: React.ReactNode }) {
  return <header className="project-title">{title}</header>;
}

function ActivitySuggestion(props: { thingToDo: string | undefined }) {
  const { thingToDo } = props;
  return (
    <div className="activity-suggestion">
      While I build this, why don't you...
      {thingToDo && (
        <motion.div initial={{ y: -500 }} animate={{ y: 0 }}>
          {thingToDo}?
        </motion.div>
      )}
    </div>
  );
}

/**
 * @example
 *         <Knob
          onDrag={({ x }) => setHueRotate(x)}
          drag="x"
          dragConstraints={{
            left: 0,
            right: (containerRect?.right ?? window.innerWidth) - 30,
          }}
        >
          ðŸŽ¨
        </Knob>
        <Knob
          onDrag={({ y }) =>
            setBrightness(
              1 - y / ((containerRect?.height ?? window.innerWidth) - 30)
            )
          }
          drag="y"
          dragConstraints={{
            top: 0,
            bottom: (containerRect?.bottom ?? window.innerHeight) - 30,
          }}
        >
          ðŸ’¡
        </Knob>
 */
function Knob({
  children,
  onDrag,
  ...draggableProps
}: React.PropsWithChildren<
  {
    onDrag: ({ x, y }: { x: number; y: number }) => void;
  } & Omit<DraggableProps, "onDrag">
>) {
  const handleDrag = useCallback(
    (_e, { point: { x, y } }: PanInfo) => {
      onDrag({ x, y });
    },
    [onDrag]
  );

  return (
    <motion.div
      className="knob"
      drag
      whileDrag={{ scale: 1.1 }}
      whileTap={{ cursor: "grabbing" }}
      onDrag={handleDrag}
      dragMomentum={false}
      {...draggableProps}
    >
      {children}
    </motion.div>
  );
}

function getRandomColor() {
  const randomHex = Math.floor(Math.random() * 16777215).toString(16);
  return `#${randomHex.length < 6 ? randomHex + "a" : randomHex}`;
}
